<template>
    <transition name="slide">
        <div class="code-img" ref="codeImg">
          <div>
            <img v-if="codeImg" :src="codeImg" @load="aaaa" alt="">
            <div class="btn-wrapper">
              <Button size="large" type="default" @click="back">返回</Button>
            </div>
          </div>
        </div>
    </transition>
</template>

<script>
import { Button } from "vant";
import BScroll from "better-scroll";
export default {
    data() {
        return {
            codeImg: ""
        };
    },
    created() {
        this.codeImg = decodeURIComponent(this.$route.query.url);
    },
    mounted() {
        this.tid = setTimeout(() => {
            this._initScroll();
        }, 20);
    },
    methods: {
        back() {
            this.$router.back();
        },
        _initScroll() {
            this.scroll = new BScroll(this.$refs.codeImg, {
                click: true
            });
        },
        aaaa() {
            if(!this.scroll) {
                return;
            }
            this.scroll.refresh();
        }
    },
    components: {
        Button
    },
    destroyed() {
        this.tid = null;
    }
};
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>



